<template>
  <el-row>
    <div class="my-card">
      <el-button></el-button>
      <v-button text="PageSchema" type="modal">
        <v-json v-model="formSchema" />
      </v-button>
      <el-button></el-button>
    </div>
    <el-card shadow="never" class="my-card">
      <template #header>
        <el-row>
          <el-col :span="12">
            <span></span>
          </el-col>
          <el-col :span="12" class="card-action">
            <v-button text="" type="modal" :btn-props="{type:'text'}" :before-close="beforeFormItemClose">
              <v-table ref="form-items" v-bind="formItemTable" />
            </v-button>
          </el-col>
        </el-row>
      </template>
      <v-form :key="formKey" v-bind="form" />
    </el-card>
  </el-row>
</template>
<script>
import VTable from '../../components/table/index'
import VForm from '../../components/form/index'
import VButton from '../../components/button/VButton'
import VJson from '../../components/form/VJson'
import form from './form'
export default {
  name: 'Entity',
  components: { VTable, VForm, VButton, VJson },
  data() {
    return {
      formKey: 1,
      tableKey: 1,
      entity: {
        name: '',
        fields: []
      },
      formItemTable: form.itemTable,
      form: {
        formItems: []
      }
    }
  },
  computed: {
    formSchema() {
      return JSON.stringify(this.form, null, 2)
    }
  },
  methods: {
    beforeFormItemClose(done) {
      this.$refs['form-items'].tableList.forEach((item, index) => {
        if (!item.field) {
          this.$message({ type: 'error', message: '' + (index + 1) + ', ' })
          return false
        }
        if (!item.type) {
          this.$message({ type: 'error', message: '' + (index + 1) + ', ' })
          return false
        }
        if (!item.label) {
          this.$message({ type: 'error', message: '' + (index + 1) + ', ' })
          return false
        }
      })
      this.form.formItems = this.$refs['form-items'].tableList
      this.formKey++
      return true
    }
  }
}
</script>
<style>
  .my-card {
    margin-bottom: 20px;
    width: 100%;
  }
  .card-action {
    display: flex;
    justify-content: flex-end
  }
</style>
