<template>
  <component :is="compType(to)" v-bind:="linkProps(to)">
    <slot />
  </component>
</template>

<script>
import { isExternal } from '../../../utils/validate'

export default {
  props: {
    to: {
      type: String,
      required: true
    }
  },
  methods: {
    compType(url) {
      if (isExternal(url)) {
        return 'a'
      } else {
        return 'router-link'
      }
    },
    linkProps(url) {
      if (isExternal(url)) {
        return {
          is: 'a',
          href: url,
          target: '_blank',
          rel: 'noopener'
        }
      }
      return {
        is: 'router-link',
        to: url
      }
    }
  }
}
</script>
